<template>
  <div class="set">
    <!-- <van-pull-refresh class="setcontainer" v-model="isLoading" @refresh="onRefresh()"> -->
        <div class='setcontainer_top'>
          <div class='bg'>
            <!-- <div class="bg-jf" @click="enterJFrule">
              <img src='../../assets/image/积分.png' style="width:0.29rem;height:0.29rem;margin-top:0;">
              <span style="margin-left:0.19rem;font-size:0.38rem;color:rgba(246,208,82,1);padding-top:0.03rem">积分</span>
            </div> -->
            <div style="display: flex;justify-content: center;align-items: center;width:100%;">
                <img :src='head' alt="" v-if="isShow" style='border-radius: 50%;' @click.stop='changeHead()'> 
                <img src='../../assets/image/头像.png' alt="" v-if="!isShow" style='border-radius: 50%;' @click.stop='changeHead()'>
            </div>
            <div class='name'>
              <div class='name_container'>
                <div style='font-weight:500;font-size:0.36rem;color:rgba(45,45,47,1);'>{{name}}</div>
                <div class='branchStyle'>{{branchName}}</div>
              </div>
            </div>
          </div>
          <div class='headPortrait'>
           <el-row class="models-style models-style-first">
             <el-col :span="8">
              <img src='../../assets/image/我的积分.png'  style='border-radius: 50%;height:0.74rem;' @click.stop='enterJFrule()'>
              <br/>
              <div style="margin-top: 0.12rem;">
                <span class="mdoel-span-style"> 积分</span>
              </div>
             </el-col>
             <el-col :span="8">
                <img src='../../assets/image/最近浏览.png'  style='border-radius: 50%;height:0.74rem;' @click.stop='rencently()'>
                <br/>
                <div style="margin-top: 0.12rem;">
                  <span class="mdoel-span-style"> 最近浏览</span>
                </div>
              </el-col>
              <el-col :span="8">
                  <img src='../../assets/image/我喜欢.png'  style='border-radius: 50%;height:0.74rem;' @click.stop='mylike()'>
                  <br/>
                  <div style="margin-top: 0.12rem;">
                    <span class="mdoel-span-style"> 我喜欢</span>                  
                  </div>
                </el-col>
           </el-row>
           <el-row class="models-style models-style-second">
              <el-col :span="8">
                  <img src='../../assets/image/题库练习.png'  style='border-radius: 50%;height:0.74rem;' @click.stop='goToTest()'>
                  <br/>
                  <div style="margin-top: 0.12rem;">
                    <span class="mdoel-span-style"> 题库练习</span>                                   
                  </div>
              </el-col>
              <el-col :span="8">
                  <img src='../../assets/image/挑战答题.png'  style='border-radius: 50%;height:0.74rem;' @click.stop='challengeExam()'>
                  <br/>
                  <div style="margin-top: 0.12rem;">
                    <span class="mdoel-span-style"> 挑战答题</span>                 
                  </div>
               </el-col>
              <el-col :span="8" >
                  <!-- <img src='../../assets/image/论坛入口.png'  style='border-radius: 50%;height:0.74rem;' @click.stop='challengeExam()'>
                  <br/>
                  <div style="margin-top: 0.12rem;">
                    <span class="mdoel-span-style">工作建议</span>                 
                  </div> -->
              </el-col>
            </el-row>
             
          </div>
        </div>
        <div class='setcontainer_center'>
          <div class='setcontainer_list' @click='enterPerson()'>
            <div class='setcontainer_left'>
              <img src="../../assets/image/个人信息.png" alt="" class='icon'>
              <span>个人信息</span>
            </div>
            <img src="../../assets/image/设置的进入.png" alt="" class='enter'>
          </div>
        </div>
        <div class='setcontainer_center' style="margin-top: 0.02rem;">
          <!-- <div class='setcontainer_list' @click='studyMess()'>
            <div class='setcontainer_left'>
              <img src="../../assets/image/需求提报.png" alt="" class='icon'>
              <span>需求提报</span>
            </div>
            <img src="../../assets/image/设置的进入.png" alt="" class='enter'>
          </div> -->
          <div class='setcontainer_list' @click='changePwd()'>
            <div class='setcontainer_left'>
              <img src="../../assets/image/修改密码.png" alt="" class='icon'>
              <span>修改密码</span>
            </div>
            <img src="../../assets/image/设置的进入.png" alt="" class='enter'>
          </div>
        </div>
        <div class='setcontainer_center' style="margin-top: 0.02rem;">
          <div class='setcontainer_list' @click='upGrade()'>
            <div class='setcontainer_left'>
              <img src="../../assets/image/系统升级.png" alt="" class='icon'>
              <span>系统升级</span>
            </div>
            <div>
              <span style="color:#888;font-size:0.32rem;margin-right:0.2rem;padding-bottom:0.1rem">V{{version}}</span>
              <img src="../../assets/image/设置的进入.png" alt="" class='enter'>
            </div>
          </div>
        </div>
        <div class="exit">
          <div class="exitButton" ><span @click.stop="exit()">退出登录</span></div>
        </div>
    <!-- </van-pull-refresh> -->
    <!-- 确认弹出框 - 是否升级 -->
    <Upgrade :dialogVisible="verDialogVisible" :newVersion="newVersion" @close="closeVerDialog"></Upgrade>
    <!-- 确认弹出框 - 已是最新版本 -->
    <!-- <Isnew :dialogVisible="isnewDialogVisible" :mess="mess"  @inNesclose="isnewcloseVerDialogMethod"></Isnew> -->
    <Isnew :mess="mess"></Isnew>
    <!-- 确认弹出框 - ios升级 -->
    <iosUpgrade :dialogVisible="iosDialogVisible" :newVersion="newVersion"></iosUpgrade>
    <!-- 确认弹出框 - 正在下载 -->
    <Download :dialogVisible="loadDialogVisible" :percentage="progress"></Download>
    <div class='bigBox'>
      <div class='smallBox'>
        <img src="../../assets/image/开始考试的背景.png" alt="">
        <div class='boxWord'>确定退出登录?</div>
        <div class="startText-mess-two">
            <div class="startText-mess-two-wait" @click.stop="cancel()">取消</div>
            <div style="width:0.021rem;height:0.42rem;background:rgba(231,236,244,1);margin-top:0.3rem;"></div>
            <div class="startText-mess-two-go" @click.stop="comfirm()">确定</div>
        </div>
      </div>
    </div>
    <div class='changeType'>
      <div class='typeBG' @click.stop='closeType()'></div>
      <div class='type'>
        <div class='selectType'>
          <div class='fromPhoto' @click.stop='selectPhoto()'>从相册选择照片</div>
          <div class='fromCamera' @click.stop='takePicture()'>拍照</div>
        </div>
        <div class='cancelSelect' @click.stop="cancelSelect()">取消</div>
      </div>
    </div>
    <!-- 加载页面 -->
    <Loading :dialogVisible="loadverDialogVisible"></Loading>
    <!-- 加载页面 -->
    <itemLoading :dialogVisible='isShowLoading'></itemLoading>

  </div>
</template>

<script>
import itemLoading from './../components/itemLoading'
import Loading from './../components/loading'
import API from '@/api/api'
import $ from 'jquery'
import Upgrade from './../components/upgrade'
import Download from './../components/download'
import iosUpgrade from './../components/iosUpgrade'
import Isnew from './../components/isnew'
import { Toast } from 'mint-ui'
import store from '@/store'
export default {
  name: 'index',
  components: { Upgrade, Download, Isnew, iosUpgrade, Loading, itemLoading },
  data () {
    return {
      isShowLoading: true,
      name: '', // 姓名
      head: '', // 头像
      branchName: '', // 部门
      version: this.GLOBAL.VERSION,
      verDialogVisible: false,
      isnewDialogVisible: false,
      iosDialogVisible: false,
      // IS_NEW:this.GLOBAL.IS_NEW,
      loadDialogVisible: false,
      newVersion: '',
      progress: 0,
      mess: '已是最新版本!',
      CONFIG: this.GLOBAL.CONFIG,
      isShow: true,
      yearJF: 0, // 今年积分
      personId: '', // 用户id
      isLoading: false, // 下拉刷新
      findUntestedCG: false,
      getPerAndOrgByUserIdCG: false,
      getselectYearScoreCG: false,
      loadverDialogVisible: false // 加载页
    }
  },
  created () {
    this.$parent.isShow = 2
    this.findUntested() // 判断是否存在未考考试
    this.getPerAndOrgByUserId() // 获取个人信息
    this.getselectYearScore() // 获取本年积分
  },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  mounted () {
    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
  },
  computed: {
    requestSuccess () {
      return this.findUntestedCG && this.getPerAndOrgByUserIdCG && this.getselectYearScoreCG
    }
  },
  watch: {
    requestSuccess (val, newval) {
      if (val) {
        this.isShowLoading = false
        setTimeout(() => {
          // this.$toast('刷新成功');
          this.isLoading = false
        }, 500)
      } else {
        setTimeout(() => {
          // this.$toast('刷新失败');
          this.isLoading = false
        }, 40000)
      }
    }
  },
  methods: {
    // 下拉刷新
    onRefresh () {
      this.findUntested()
      this.getPerAndOrgByUserId()
      this.getselectYearScore()
    },
    // 进入需求提报
    enterDemand () {
      this.$router.push({
        name: 'demand_list',
        params: {
          personId: this.personId,
          name: this.name
        }
      })
    },
    // 判断是否存在未考考试
    findUntested () {
      this.findUntestedCG = false
      API.post('findUntested', {
      }, 'json', (data, err) => {
        if (data.success) {
          if (data.data > 0) {
            $('.red_remind').css('display', 'flex')
          } else {
            $('.red_remind').css('display', 'none')
          }
          this.findUntestedCG = true
        } else {
          this.$toast(data.msg)
        }
      })
    },
    // 物理返回键
    onBackKeyDown () {
      if ($('.set .bigBox').css('display') === 'flex') { // 判断是否存在退出登录弹窗
        $('.bigBox').css('display', 'none')
      } else {
        if ($('.isNew').css('display') === 'flex') { // 判断是否存在是最新版本弹窗
          $('.isNew').css('display', 'none')
        } else {
          if ($('.changeType').css('display') === 'flex') { // 判断底部更改头像弹窗是否存在
            this.cancelSelect()
          } else {
            navigator.Backbutton.goHome()
          }
        }
      }
    },
    // 点击黑暗部分关闭
    closeType () {
      $('.changeType').css('display', 'none')
      $('.type').animate({ bottom: '-3.34rem' }, 1)
    },
    // 更改头像
    changeHead () {
      $('.changeType').css('display', 'flex')
      $('.type').animate({ bottom: '0rem' })
    },
    // 取消更换头像
    cancelSelect () {
      $('.changeType').css('display', 'none')
      $('.type').animate({ bottom: '-3.34rem' }, 1)
    },
    // 从相册选择
    selectPhoto () {
      $('.changeType').css('display', 'none')
      $('.type').animate({ bottom: '-3.34rem' }, 1)
      navigator.camera.getPicture(this.onSuccess, this.onFail, {
        quality: 50,
        // eslint-disable-next-line no-undef
        destinationType: Camera.DestinationType.FILE_URI,
        sourceType: 0,
        correctOrientation: true
      })
    },
    // 拍照
    takePicture () {
      $('.changeType').css('display', 'none')
      $('.type').animate({ bottom: '-3.34rem' }, 1)
      // cordova 拍照功能
      navigator.camera.getPicture(this.onSuccess, this.onFail, {
        quality: 50,
        // eslint-disable-next-line no-undef
        destinationType: Camera.DestinationType.FILE_URI,
        // eslint-disable-next-line no-undef
        cameraDirection: Camera.Direction.FRONT,
        sourceType: 1,
        correctOrientation: true
      })
    },
    // 拍照成功回调
    onSuccess (imageURI) {
      const that = this
      // eslint-disable-next-line no-undef
      var options = new FileUploadOptions()
      options.fileKey = 'file'
      // if(imageURI.indexOf("content:")>=0){
      //     options.fileName="random.jpg"
      // }else{
      options.fileName = imageURI.substring(imageURI.lastIndexOf('/') + 1)
      // }
      options.mimeType = 'text/plain'
      options.httpMethod = 'POST'
      let sha256 = require('js-sha256').sha256
      options.params = { // 请求参数 键值对
        url: this.GLOBAL.UPLOAD.UPLOAD_URL,
        appId: this.GLOBAL.UPLOAD.UPLOAD_APPID,
        timestamp: Date.now(),
        sig: sha256(this.GLOBAL.UPLOAD.UPLOAD_APPID + this.GLOBAL.UPLOAD.UPLOAD_SECRET + Date.now())
      }
      // cordova文件上传
      // eslint-disable-next-line no-undef
      var ft = new FileTransfer()
      ft.upload(
        imageURI,
        encodeURI(`${this.GLOBAL.UPLOAD.UPLOAD_URL}/pxks/upload`),
        (r) => {
          // if(r.success){
          // eslint-disable-next-line no-eval
          var data = eval('(' + r.response + ')').data
          that.head = `${this.GLOBAL.UPLOAD.UPLOAD_URL}/pxks/dl/` + data
          API.post('modify', {
            id: that.personId,
            head: that.head
          }, 'json', (data, err) => {
            if (data.success) {
              that.getPerAndOrgByUserId()
            } else {
              that.$toast('头像上传失败')
            }
          })
          // }else{
          //   that.$toast('头像上传失败');
          // }
        },
        function () {
          that.$toast('头像上传失败')
        },
        options
      )
    },
    // 拍照失败回调
    onFail: function (message) {
      // this.$toast(message)
    },
    // 获取个人信息
    getPerAndOrgByUserId () {
      this.getPerAndOrgByUserIdCG = false
      API.post('getPerAndOrgByUserId', {
      }, 'json', (data, err) => {
        if (data.success) {
          this.name = data.data.personnel.name
          this.branchName = data.data.branchName
          this.personId = data.data.personnel.id
          localStorage.setItem('personId', this.personId)
          localStorage.setItem('personName', this.name)
          if (data.data.personnel.head != null && data.data.personnel.head !== '') {
            this.head = data.data.personnel.head
            this.isShow = true
          } else {
            this.head = '../../assets/image/头像.png'
            this.isShow = false
          }
          this.getPerAndOrgByUserIdCG = true
        } else {
          this.$toast(data.msg)
        }
      })
    },
    // 获取本年积分
    getselectYearScore () {
      this.getselectYearScoreCG = false
      API.post('selectYearScore', {
      }, 'json', (data, err) => {
        if (data.success) {
          this.yearJF = data.data
          this.getselectYearScoreCG = true
        } else {
          this.$toast(data.msg)
        }
      })
    },
    // 进入考试
    enterTextList () {
      if (JSON.parse(JSON.stringify(this.$store.state.spe.speMap)).length === 0) {
        this.$toast('暂无试题')
      } else {
        this.$router.push('/challenge_start')
      }
      // this.$router.push('/text_list_wait')
    },
    // 进入个人信息
    enterPerson () {
      this.$router.push('/text_person')
    },
    // 进入修改密码
    changePwd () {
      this.$router.push('/change_pwd')
    },
    // 进入积分规则
    enterJFrule () {
      this.$router.push('/jf_rule')
    },
    // 退出按钮
    exit () {
      $('.bigBox').css('display', 'flex')
      // $('.smallBox').css('display','block')
    },
    // isnewcloseVerDialogMethod(){
    //   // this.GLOBAL.IS_NEW = false;
    //   this.isnewDialogVisible = false;
    // },
    // 取消按钮
    cancel () {
      $('.bigBox').css('display', 'none')
    },
    // 确定按钮
    comfirm () {
      $('.bigBox').css('display', 'none')
      // this.GLOBAL.isLogin = false;
      localStorage.setItem('isLogin', false)
      this.GLOBAL.USER_INFO = ''
      this.$router.replace('/login')
      localStorage.removeItem('username')
      localStorage.removeItem('password')
      localStorage.removeItem('activeItem')
      localStorage.removeItem('classifyList')
      localStorage.removeItem('personId')
      localStorage.removeItem('personName')
      localStorage.removeItem('dataListSpecialCode')
    },
    // 获取版本号
    getNewVersion () {
      API.post('getNewVersion', {
      }, 'json', (data, err) => {
        if (data.success) {
          this.newVersion = data.data
          if (this.newVersion !== this.version) { // 判断版本号
            var u = navigator.userAgent // 判断是安卓还是ios
            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 // g
            var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
            if (isAndroid) {
              console.log('Andriod')
              this.verDialogVisible = true
            }
            if (isIOS) {
              console.log('iOS')
              this.iosDialogVisible = true
            }
          } else {
            $('.isNew').css('display', 'flex')
          }
        } else {
          this.$toast(data.msg)
        }
      })
    },
    // 升级
    upGrade () {
      this.getNewVersion()
      // this.hasPermission()
    },
    /**
         * 校验app是否有安卓写入权限
         */
    hasPermission () {
      const _this = this
      // eslint-disable-next-line no-undef
      let permissions = cordova.plugins.permissions
      // 检查app写入权限
      permissions.checkPermission(permissions.WRITE_EXTERNAL_STORAGE, (s) => {
        if (s.hasPermission) {
          _this.downloadNewVer()
          return
        }
        // 申请app写入权限
        permissions.requestPermission(permissions.WRITE_EXTERNAL_STORAGE,
          (s) => {
          // 申请成功
            if (s.hasPermission) {
              _this.downloadNewVer()
              return
            }
            // 申请失败
            Toast('请开启本应用存储权限')
          },
          // eslint-disable-next-line handle-callback-err
          (error) => {
            Toast('请开启本应用存储权限')
          }
        )
      // eslint-disable-next-line handle-callback-err
      }, (error) => {
        Toast('请开启本应用存储权限')
      })
    },
    /**
         * 下载最新版本apk
         */
    downloadNewVer () {
      // eslint-disable-next-line no-undef
      let fileTransfer = new FileTransfer()
      let num = 0
      let flag = false
      const that = this
      fileTransfer.onprogress = (e) => {
        // alert('onprogress');
        // alert(JSON.stringify(e));
        if (!e.lengthComputable) { // 进度信息是否可用
          return
        }
        num = e.loaded / e.total * 100
        if (flag) {
          return
        }
        that.loadDialogVisible = true
        flag = true
      }

      let timer = setInterval(() => {
        Math.round(num) > this.progress && (this.progress = Math.round(num))
        // cordova.plugins.notification.local.schedule({
        //   title: '培训考试',
        //   text: '正在下载：' + Math.round(num) + '%',
        //   foreground: true
        // });
        num >= 100 && clearInterval(timer)
      }, 100)

      fileTransfer.download(
        // uri网络下载路径
        encodeURI('http://' + this.CONFIG + '/pxks/apk' + '/pxks.apk'),
        // 文件本地存储路径
        encodeURI('file:///storage/emulated/0/pxks/apk' + '/pxks.apk'),
        // 下载成功
        (entry) => {
          this.loadDialogVisible = false
          this.fileOpener()
        },
        // eslint-disable-next-line handle-callback-err
        (error) => { // 下载失败
          this.loadDialogVisible = false
          // this.reDLDialogVisible = true;
        }
      )
    },
    /**
         * 打开下载的apk文件
         */
    fileOpener () {
      // eslint-disable-next-line no-undef
      cordova.plugins.fileOpener2.open(
        '/storage/emulated/0/pxks/apk/pxks.apk',
        'application/vnd.android.package-archive',
        {
          error: (e) => {
            Toast('安装包打开失败，请手动安装')
          },
          success: () => {}
        }
      )
    },
    /**
         * 关闭升级提醒弹出框
         */
    closeVerDialog (val) {
      this.verDialogVisible = false
      // this.reDLDialogVisible = false;
      if (!val) {
        return
      }
      this.hasPermission()
    },
    // 进入个人成绩排行榜
    studyMess () {
      this.$router.push('/demand_list')
    },
      // 进入最近浏览
      rencently () {
      this.$router.push({
        name: 'recently_look'
      })
    },
    // 进入我喜欢
    mylike () {
      this.$router.push({
        name: 'me_like'
      })
    },
   /*
   *进入挑战答题
   */
   challengeExam () {
    if(this.$store.state.spe.speMap.length === 0){
      this.$toast('不存在专项')
    }else{
      this.$router.push({
        name: 'challenge_start'
      })
    }
    
   },
  /*
  *进入题库练习
  **/
   goToTest () {
    store.dispatch('getAllspe').then(() => {
      if(this.$store.state.spe.speMap.length === 0){
        this.$toast('不存在试题')
      }else{
        localStorage.removeItem("exercisePageclassifyList"); // 存储最新的专项列表
        localStorage.removeItem("exercisePageactiveItem"); // 存储当前一级专项的状态
        localStorage.removeItem("exercisePageSpecialCode");   
        this.$router.push({
          name: 'exercisePage'
        })
      }
    })
   },
    goJfRule () {

    }
  }
}
</script>

<style scoped>
  .set{
    width:100%;
    height:calc(100vh - 0.98rem);
    background: #F5F6F8;
    overflow:scroll;
  }
  .set .setcontainer{
    height:calc(100vh - 0.98rem);
    margin-bottom:0.98rem;
    width:100%;
    overflow:scroll;
    -webkit-overflow-scrolling: touch;
    /* display: flex;
    justify-content: space-between; */
    /* align-items: center; */
  }
  .set .setcontainer_top{
    height:7.31rem;
    background-color: #FFFFFF;
    position: relative;
    background-image: url('../../assets/image/设置背景.png');
    background-size: 100% 100%;
    margin-bottom: 0.16rem;
  }
  /* .headPortrait {
   
  } */
  .models-style{
    height: 1.5rem;
    margin-right: 0.2rem;
    margin-left: 0.2rem;
    text-align: center ;
  }
  .models-style-first{
    margin-top: 0.3rem;
  }
  .models-style-second{
    margin-bottom: 0.3rem;
  }
  .mdoel-span-style {
    color: #6E747E;
    font-size: 0.25rem;
    padding-top: 0.3rem;
  }
  .set .setcontainer_top .bg{
    width:100%;
    height:1.79rem;
    padding-top: 0.7rem;
    background-image: url('../../assets/image/我的页背景.png');
    background-size: 7.5rem 2.58rem;
  }
  .set .setcontainer_top .bg img{
    width: 1.95rem;
    height: 1.95rem;
  }
  .set .setcontainer_top .bg .name{
    font-size:0.36rem;
    color: rgb(233, 92, 92);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top:0.32rem;
  }
  .name_container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .branchStyle{
    padding-top:0.25rem;
    color:#A3B2C5;
    /* background:linear-gradient(180deg,rgba(87,148,246,0.13) 0%,rgba(87,148,246,0.13) 100%); */
    /* border-radius:0.06rem; */
    font-size:0.24rem;
    letter-spacing: 0.02rem;
  }
  .set .setcontainer_top .bg .dept{
    font-size:0.3rem;
    color: #FFFFFF;
    font-weight:400;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top:0.2rem;
  }

  .set .setcontainer_top .headPortrait{
    height:5.57rem;
    background-color: #FFFFFF;   
    padding-top: 2.2rem   ;
    width:100%;
    /* height:4.5rem; */
    /* display: flex; */
    /* justify-content: center;
    align-items: center; */
    border: 0rem solid;
    border-top-left-radius: 0.35rem;
    border-top-right-radius: 0.35rem;
  }

  .set .setcontainer_top .headPortrait .person{
    width: 100%;
    display: flex;
    /* justify-content: space-evenly; */
    align-items: center;
    position: absolute;
    bottom: 0.41rem;
    left:0;
  }
  .set .setcontainer_top .headPortrait .person .personcaozuo{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width:calc((100vw - 0.021rem)/2);
  }
  .set .setcontainer_top .headPortrait .person img{
    display: block;
    width:0.9rem;
    height:0.9rem;
  }
  .set .setcontainer_top .headPortrait .person .red_remind{
    width:0.18rem;
    height:0.18rem;
    background:rgba(251,108,108,1);
    border-radius:50%;
    margin-left: 1rem;
    margin-bottom: -0.15rem;
    display: none;
    position: absolute;
  }
  .set .setcontainer_top .headPortrait .person .name{
    padding-top:0.05rem;
    margin-top:0.14rem;
    display: block;
    width:2.53rem;
    font-size:0.32rem;
    color:#fff;
    text-align: center;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    height:0.66rem;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    /*! autoprefixer: off */
      /* autoprefixer: on */
  }
  .set .setcontainer_center{
    padding-left: 0.35rem;
    padding-right: 0.35rem;
    background: #ffffff;
  }
  .set .setcontainer_center .setcontainer_list{
    height: 1.06rem;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom:0.01rem solid rgba(238,238,238,1);
    position: relative;
  }
  .set .setcontainer_center .setcontainer_list:last-child{
    border:0;
  }
  .set .setcontainer_center .setcontainer_list .setcontainer_left{
    display: flex;
    align-items: center;
  }
  .set .setcontainer_center .setcontainer_list .setcontainer_left .icon{
    width:0.4rem;
    height:0.4rem;
  }
  .set .setcontainer_center .setcontainer_list .setcontainer_left span{
    margin-left:0.14rem;
    font-size:0.34rem;
    color:rgba(56,65,80,1);
    padding-top:0.03rem;
  }
  .set .setcontainer_center div .enter{
    width:0.15rem;
    height:0.31rem;
  }
  .exit{
    display: flex;
    width:100%;
    padding:0 0.8rem;
    /* margin-top:0.3rem; */
    /* height: calc(100% - 11.7rem); */
    height:1rem;
    justify-content: center;
    align-items: center;
  }
  .exitButton{
    width:100%;
    height:0.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .exitButton span{
    /* padding:0.26rem 0 0.26rem 0; */
    display: inline-block;
    font-size:0.36rem;
    line-height: 0.36rem;
    color:#FF775B;
    /* margin-top: 0.43rem; */
  }
  .bigBox{
    z-index: 2500;
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.7);
    position: fixed;
    top:0;
    display: none;
    justify-content: center;
    align-items: center;
  }
  .smallBox{
    position: relative;
    z-index: 999;
    width:5.5rem;
    height:2.74rem;
  }
  .smallBox img{
    width:5.5rem;
    height:2.74rem;
  }
  .smallBox .boxWord{
    position:absolute;
    width:100%;
    /* height:1.74rem; */
    display: flex;
    justify-content: center;
    /* align-items: center; */
    text-align: center;
    top:0.72rem;
    left:0;
    z-index: 1000;
    font-size:0.34rem;
    color:#4E4E4E;
    letter-spacing:0.04rem;
  }
  .smallBox .button{
    width:100%;
    height:0.78rem;
    position:absolute;
    bottom:0.5rem;
    left:0;
    text-align:center;
    z-index:1000;
    padding:0 0.4rem;
    display: flex;
    justify-content: space-between
  }
  .button div{
    width:2.18rem;
    height:0.78rem;
    /* display: inline-block; */
    font-size:0.3rem;

  }
  .button .cancel{
    color:#666666;
    background:#ffffff;
    border:0.02rem solid rgba(221,221,221,1);
    border-radius: 0.39rem;
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .button .comfirm{
    color:#ffffff;
    background:#04CD9D ;
    border-radius: 0.39rem;
    border:0.02rem solid #04CD9D;
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .bg-jf{
    font-size: 0.36rem;
    width: 1.76rem;
    height: 0.73rem;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-left: 0 solid;
    border-bottom-left-radius: 0.5rem;
    border-top-left-radius: 0.5rem;
    position: relative;
    float: right;
    top: 0.6rem;
  }
  .set .startText-mess-two{
      width: 100%;
      height: 1rem;
      position: absolute;
      z-index: 999;
      display: flex;
      justify-content: space-around;
      align-content: center;
      bottom: 0;
      left:0;
      font-size: 0.34rem;
      border-top: 0.012rem solid #E7ECF4;
  }
  .set .startText-mess-two-wait{
      width: 48%;
      height: 100%;
      color:rgba(110,116,126,1);
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 0.34rem;
  }
  .set .startText-mess-two-go{
      width: 48%;
      height: 100%;
      color:rgba(87, 148, 246, 1);
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 0.34rem;
  }
  .changeType{
    z-index: 99;
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.5);
    position: fixed;
    top:0;
    display: none;
    justify-content: center;
    align-items: center;
  }
  .changeType .type{
    position: absolute;
    bottom:-3.34rem;
    left:0;
    z-index: 999;
    width:100%;
    height:3.34rem;
  }
  .changeType .selectType{
    width:100%;
    padding:0 0.35rem;
    background: #fff;
    margin-bottom: 0.16rem;
  }
  .changeType .selectType div{
    font-size:0.34rem;
    color:#384150;
    display: flex;
    align-items: center;
    justify-content: center;
    height:1.06rem;
    width:100%;
  }
  .changeType .selectType .fromPhoto{
    border-bottom: 0.012rem solid rgba(243,243,243,1);
  }
  .changeType .cancelSelect{
    background: #fff;
    width:100%;
    height:1.06rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color:#FF775B;
    font-size: 0.36rem;
    letter-spacing: 0.02rem;
  }
  .changeType .typeBG{
    position: absolute;
    top:0;
    left:0;
    height:100%;
    width:100%;
    z-index: 100;
  }
 
</style>
<style>
  .set .setcontainer .van-loading--circular{
    background:#5794F6 !important;
  }
  .set .setcontainer .van-pull-refresh__head{
    background:#5794F6 !important;
  }
</style>



