<template>
  <el-dialog
    class="load"
    :visible.sync="dialogVisible"
    :modal-append-to-body="false"
    :close-on-click-modal="true"
    :before-close="handleClose"
    top="0"
    :center="true"
    :show-close="false">
    <div class="dialog-body-img"><img src="../../assets/image/弹窗背景@2x.png" alt=""></div>
    <div class="dialog-body-text"><span>正在为您更新，请耐心等待</span></div>
    <el-progress :percentage="percentage" color="rgba(115, 223, 233, 1)" :text-inside="true"></el-progress>
  </el-dialog>
</template>

<script>
export default {
  props: ['dialogVisible', 'percentage'],
  methods: {
    handleClose (done) {
      done()
      this.$parent.loadDialogVisible = false
    }
  }
}
</script>

<style lang="less">
  .load{
    width:100%;
    height:100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top:0;
    left:0;
  }
  .load .el-dialog__wrapper{
    position: absolute !important;
    top:0 !important;
    left:0;
  }
  .load .el-dialog__header{
    padding:0;
  }
  .load .el-dialog__body{
    position: relative;
    width:5.54rem;
    height:4.66rem;
    text-align: center
  }
  .load .el-dialog{
    position: relative;
    width:5.54rem;
    height:4.66rem;
    margin:0;
  }
  .load .dialog-body-img{
    width:5.54rem;
    height:4.66rem;
    display: flex;
    align-items: center;
    justify-content: center;
    position:absolute;
    left:0;
    top:0;
  }
  .load .dialog-body-img img{
    text-align: center;
    width:5.54rem;
    height:4.66rem;
  }
  .load .dialog-body-text{
    position: absolute;
    font-size:0.32rem;
    top:2.92rem;
    width:100%;
    text-align: center;
    left:0;
    font-weight: normal;
  }
  .load .dialog-body-text span{
    // width:4.8rem;
    display: inline-block;
    text-align: center;
    color:#4E4E4E;
  }
  .load .el-progress{
    position: absolute;
    bottom: 0.72rem;
    padding:0 0.66rem;
    width:100%;
    left:0;
  }
  .load .el-progress-bar{
    width:100%;
  }
  .load .el-progress-bar__outer{
    width:100%;
    height:0.17rem !important;
  }
  .load .el-progress-bar__inner{
    height: 100%;
  }
  .load .el-progress {
    display: flex;
    justify-content: center;
  }
  .load .el-progress-bar {
    color:rgba(115, 223, 233, 1);
    border:0.01rem solid #F0F5F6;
  }
  .load .el-progress-bar__innerText {
    color: rgba(115, 223, 233, 1);
    font-size:0.28rem;
    position: absolute;
    top: -0.3rem;
    left:0;
  }
// .load {
//   display: flex;
//   align-items: center;
//   .el-dialog {
//     width: 22vw;
//     margin-bottom: 0;
//     border-radius: 0.625rem;
//   }
//   .el-dialog__header, .el-dialog__footer {
//     display: none;
//   }
//   .el-dialog__body {
//     width: calc(100% - 4rem);
//     padding: 2rem !important;
//     font-size: 1.75rem;
//     color: #222222;
//   }
//   .dialog-body-text {
//     width: 100%;
//     text-align: center;
//     margin-bottom: 1.5rem;
//   }

// }
</style>



